@import "../partials/variables";

//Widgets Styles
.atw_wrapper{
    .atw_widget{
        margin: 30px 0;
    }
    .atw_widget_title{
        h3{
            font-size: 20px;
            font-weight: 400;
            margin-bottom: 30px;
        }
    }
    .atw_widget-style1{
        border: 1px solid $borderColor;
        padding: 20px;
        .atw_sp-single{
            display: flex;
            border-bottom: 1px solid $borderColor;
            margin-bottom: 20px;
            padding-bottom: 20px;
            &:last-child{
                border-bottom: 0;
                margin-bottom: 0;
                padding-bottom: 0;
            }
            &:hover{
                figure{
                    figcaption{
                        opacity: 1;
                        visibility: visible;
                        transform: scale(1);
                    }
                }
            }
            figure{
                position: relative;
                margin-right: 15px;
                figcaption{
                    position: absolute;
                    width: 100%;
                    left: 0;
                    top: 0;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: rgba(0,0,0,0.5);
                    visibility: hidden;
                    opacity: 0;
                    transform: scale(0.8);
                    transition: $transition;
                    a{
                        display: inline-block;
                        padding: 10px 12px;
                        background: #fff;
                        font-size: 15px;
                        color: $theme1;
                    }
                }
            }
            .atw_sp-details{
                .atw_item_title{
                    margin-bottom: 10px;
                    a{
                        font-size: 15px;
                        font-weight: 400;
                        color: #000;
                        transition: $transition;
                        &:hover{
                            color: $theme1;
                        }
                    }
                }
                .atw_price{
                    font-size: 15px;
                    color: $theme1;
                    display: block;
                    margin-bottom: 10px;
                    del{
                        color: #9192a3;
                        display: inline-block;
                        padding-left: 5px;
                    }
                }
            }
        }
    }

    .atw_widget-style2{
        position: relative;
        .atw_widget_slide1{
            .atw_widget_slide-single{
                .atw_sp-single{
                    display: flex;
                    border-bottom: 1px solid $borderColor;
                    margin-bottom: 20px;
                    padding-bottom: 20px;
                    &:last-child{
                        border-bottom: 0;
                        margin-bottom: 0;
                        padding-bottom: 0;
                    }
                    &:hover{
                        .atw_sp-details{
                            .atw_rating{
                                visibility: hidden;
                                opacity: 0;
                            }
                            .atw_btn{
                                bottom: 0;
                                visibility: visible;
                                opacity: 1;
                            }
                        }
                    }
                    figure{
                        position: relative;
                        margin-right: 20px;
                    }
                    .atw_sp-details{
                        position: relative;
                        .atw_item_title{
                            margin-bottom: 10px;
                            a{
                                font-size: 15px;
                                font-weight: 400;
                                color: #000;
                                transition: $transition;
                                &:hover{
                                    color: $theme1;
                                }
                            }
                        }
                        .atw_price{
                            font-size: 15px;
                            color: $theme1;
                            display: block;
                            margin-bottom: 10px;
                            del{
                                color: #9192a3;
                                display: inline-block;
                                padding-left: 5px;
                            }
                        }
                        .atw_rating{
                            transition: $transition;
                        }
                        .atw_btn{
                            position: absolute;
                            width: 100%;
                            left: 0;
                            bottom: -20px;
                            transition: $transition;
                            visibility: hidden;
                            opacity: 0;
                        }
                    }
                }
            }
        }
        .atw_slider_controls{
            position: absolute;
            right: 0;
            top: -50px;
            .widget_slider--control1{
                cursor: pointer;
                font-size: 14px;
                display:inline-block;
                transition: $transition;
                &.icon-arrow-left{
                    margin-right: 10px;
                }
                &:hover{
                    color: $theme1;
                }
            }
        }
    }

    .atw_widget-style3{
        @extend .atw_widget-style1;
        padding: 0;
        border: 0 none;
        position: relative;
        .atw_sp-single{
            border: 1px solid $borderColor;
            padding: 16px 10px;
            border-radius: 5px;
            &:last-child{
                border-bottom: 1px solid $borderColor;
                padding-bottom: 20px;
            }
            figure{
                position: relative;
                .float-d-ratio{
                    position: absolute;
                    left: 0;
                    top: 0;
                    font-size: 12px;
                    line-height: 22px;
                }
                figcaption{
                    background: transparentize($theme2, 0.5);
                    border-radius: 5px;
                    a{
                        border-radius: 3px;
                        color: $theme2;
                    }
                }
            }
            .atw_sp-details{
                .atw_item_title{
                    a{
                        &:hover{
                            color: $theme2;
                        }
                    }
                }
                .atw_price{
                    color: $theme2;
                }
            }
        }
        .atw_slider_controls{
            position: absolute;
            right: 0;
            top: -50px;
            .widget_slider--control2{
                cursor: pointer;
                font-size: 14px;
                display:inline-block;
                transition: $transition;
                &.icon-arrow-left{
                    margin-right: 10px;
                }
                &:hover{
                    color: $theme2;
                }
            }
        }
    }

    .atw_widget_title2{
        background: #fff;
        padding:0 20px;
        h3{
            line-height:50px;
        }
    }

    .atw_widget-style4{
        position: relative;
        .atw_widget_slide-single{
            @extend .atw_widget-style1;
            background: #fff;
            border: 0 none;
        }
        .atw_slider_controls{
            position: absolute;
            right: 19px;
            top: -71px;
            .widget_slider--control3{
                cursor: pointer;
                font-size: 12px;
                display:inline-block;
                transition: $transition;
                border: 1px solid $borderColor;
                line-height: 30px;
                padding: 0 10px;
                &.icon-arrow-left{
                    margin-right: 10px;
                }
                &:hover{
                    color: #fff;
                    background: $theme1;
                    border-color: $theme1;
                }
            }
        }
    }

    .atw_widget-style5{
        @extend .atw_widget-style1;
        padding: 0;
        border: 0 none;
        .atw_sp-single{
            background: #fff;
            border: 0 none;
            padding:20px;
            margin-bottom: 10px;
            box-shadow: 0 5px 5px rgba(134,152,164,0.1);
            &:last-child{
                padding-bottom: 20px;
            }
            figure{
                figcaption{
                    a{
                        color: $theme2;
                    }
                }
            }
            .atw_sp-details{
                .atw_item_title{
                    a{
                        &:hover{
                            color: $theme2;
                        }
                    }
                }
                .atw_price{
                    color: $theme2;
                }
            }
        }
    }
}
